body {
    font-family: "Archivo";
    margin: 0;
}

/* archivo-100 - latin */
@font-face {
    font-family: "Archivo";
    font-style: normal;
    font-weight: 100;
    src: local(""), url("./fonts/archivo-v9-latin-100.woff2") format("woff2"),
        /* Chrome 26+, Opera 23+, Firefox 39+ */
            url("./fonts/archivo-v9-latin-100.woff") format("woff"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
